/* Space out content a bit */
body {
  padding-top: 20px;
}

.header {
  border-bottom: 1px solid $light-gray;
  padding-bottom: 10px;
  margin-bottom: 20px;
  text-align: center;
}

.footer {
  /* padding-top: 19px; */
  color: $dark-gray;
  border-top: 1px solid $light-gray;
  text-align: center;
  font-size: 0.8em;

  a:link, a:visited {
    color: $dark-gray;
  }
}

.fas, .fab {
  color: $dark-gray;
}

a > .fas, a > .fab {
  // make sure that font-awesome icons are underlined in links
  display: inline;
}

