/* Jumbotron */
.jumbotron {
  text-align: justify;
  padding-top: 1.5em;
  padding-bottom: 0.5em;
  font-size: 125%
}

/* Group logo */
#logo {
  width: 154px;
  height: 35px;
  margin: 2px 0 8px 0;
}

/* Front page news. */
ul.news {

  .date {
    color: $dark-gray;
    font-weight: bold;
  }

  > li {
    margin-top: 1em;
  }
}

@media (min-width: 576px) {
  .project-people {
    column-count: 2
  }
  .card-columns {
    column-count: 2
  }
}

@media (min-width: 768px) {
  .role.grad {
    column-count: 2
  }
  .project-people {
    column-count: 3
  }
  .card-columns {
    column-count: 3
  }
}

.title img {
  max-width: 50%;
}

section {
  margin-bottom: 2em;
}

.profile {
  object-fit: cover; // makes sure it's cropped
}

.noprofile {
  circle {
    fill: $light-gray;
  }

  text {
    font-weight: 700;
    fill: white;
  }
}

.card {

  .profile {
    height: 40px;
    width: 40px;
    margin: 3px;
  }

  .card-footer {
    padding: 0.5em
  }
}

.person {

  margin-bottom: 10px;

  .profile {
    height: 70px;
    width: 70px;
  }

  .bio {
    color: $dark-gray;
  }

  .person-name {
    margin: 0;
  }
}

.person-without-image {
  margin: 0;

  .bio {
    margin-left: 1em;
  }
}

svg {
  // make the alignment just like img from bootstrap's reboot.scss
  vertical-align: middle;
}
